/*
 * Main layouts for our pages
 *
 *
 * $this includes:
 * header Layout
 * Navigation Layout
 * Grids
 * Footer
 *
 */



 /* ==========================================================================
   Layout Styles: 
   ========================================================================== */

.mt-layout-admin-login{
  height: 100%;
  width: 100%;
  min-height: 100vh;

  & form.login-form{
    margin-top: 80px;
      color: #a4aab2;
      font-size: 13px;
  }
}
.mt-login-content{
  margin: 0 80px;
  margin-top:35%;
}

.mt-admin-login-title{
  font-size: 30px;
    font-weight: 300;
    color: map-get($ui-color, darkbrown);
    padding: 25px 0;
}

.admin-main-wrapper{
    background: #eee;
}

/* ==========================================================================
  Dashboard: 
   ==========================================================================*/

   .call-admin-head{
    display: block;
    padding: 1em;
   }

   .call-admin-head :first-child{
      float: left;
   }

   .call-admin-head :last-child{
      float:right;
   }

   ul.admin-call-icons li, .performance-title-wrapper>*{
    display: -webkit-inline-flex;
    display: -ms-inline-flexbox;
    display: inline-flex;
   }

.admin-call-icons>li>a{
    padding: 0;
    height: 45px;
    width: 45px;
    border-radius: 25px;
    margin: 0 0.5em;

    &:hover{
      background-color:rgba(0, 0, 0, 0.06);
    }

    & i {
      @extend .icon-center !optional;
      font-size: 1.8em;
      color: map-get($ui-color, redish);
    }
}

.performance-title-wrapper{
  padding: 1em;
  color: #8C8C8C;
}

.performance-title-wrapper i{
  font-size: 1.5em;
}
.mt-md-title{
  font-size: 1.3em;
    margin-left: 5px;
    margin-top: 2px;
}

.dashboard-cards-wrapper>*{
  margin:10px 10px 10px 0;
}
.mdl-card>:first-child{
  width: 100%;
    min-height: 200px;
}

.mdl-card>:last-child{
  min-height: 80px;
}

.chart-text-addups>li{
    text-align:center;
    & span{
        display: block;
          font-size: 18px;
          color: #8A8A8A;
          padding: 10px;
    }
}

.chart-text-addups>:first-child{
        border-right: 1px solid #eee;
}

.chart-text-addups div{
  font-size: 0.8em;
    color: #505050;
}


/*Feeds*/

.feeds-card{
  padding: 1em;
}

.admin-feeds-header{
  min-height: 70px !important;
  border-bottom: 1px solid #eee;
  padding: 1em;
  margin-bottom: 10px;
}

.feeds-wrapper{
  min-height: 200px;
}
.feeds>li{
    background-color: #fafafa;
    color: #82949a;
    margin-bottom: 7px;
    font-size: 0.9em;

}
.todos-header{
    padding: 15px 20px;
    background: #FAFAFA;
    width: 104% !important;
    min-height: 47px !important;
    border-bottom: 1px solid #eee;
}


/*Aside*/

.action-feed{
      background: #eee;
    padding: 0 1em;
}

.feed-header{
      background: #202024;
    padding: 1.5em 1em;
    margin-bottom: 10px;
}

.feed-header>h3{
      display: inline-block;
    padding: 0.2em;
    font-weight: 600;
    color: #948E8E;
}

.feed-header>span{
      display: inline-block;
    float: right;
    color: map-get($ui-color, redish);

    & i{
          font-size: 1.5em;
    }
}

.mt-header-shotEdit{
    padding: 0.5em 0.5em;
    border-bottom: 1px solid #EEE;
    display: flex;
    height: 100px;
}

/*shots page*/

header.admin-shots-head{
  width: 100%;
    padding: 1em;
}

.admin-shots-wrapper{
  width: 100%;
}

.shot-item{
    box-sizing: border-box;
    margin: 8px;
    height: 300px;
    overflow:hidden;
    
    &:active{
  box-shadow: 0px 0px 6px 3px #03A9F4;
}

}

.shot-item figure>img{
  min-height:300px;
}

/*Uploads*/
.upload-form-actions-wrapper{
    border-bottom: 1px solid #EDEDED;
    margin-bottom: 20px;
}

.upload-form-actions>span.fileinput-button{
    background: #32c5d2;
}

.upload-form-actions>button{
      font-size: 16px;
    margin: 0 17px;
}

.upload-actions button{
        font-size: 14px;
        cursor: pointer;
}